module.exports = "\r\n.mid_row h2{\r\n  padding-top: 2vh;\r\n  font-size: 10vw;\r\n  background-color: rgba(0, 0, 0, 0);\r\n  text-shadow: 0px 0px 10px black;\r\n}\r\n\r\n\r\n.container{\r\n  width: 90vw;\r\n  max-width: 100vw;\r\n  /* o materialize tava limitando a width do meu contaniner, tive que por a max-width pra conseguir aumentar ele */\r\n}\r\n\r\n\r\n.gallery{\r\n  display: flex;\r\n  justify-content: space-around;\r\n  align-items: center;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n\r\n.gallery img{\r\n  max-width: 20vh;\r\n  /* max-width com vh fica responnsivo :D */\r\n  min-height: 3vh;\r\n  /* aqui eu seto um tamanho mínimo e um máximo para as \"thumbnails\" das artes, não pus min e max para heigth e width pq elas ficavam distorcidas */\r\n  padding: 0;\r\n}\r\n\r\n\r\n.gallery img {\r\n    margin: 1vh 2vw;\r\n    box-shadow: 0px 0px 10px black;\r\n}\r\n\r\n\r\n.gallery img:hover {\r\n    transition: all 1s ease;\r\n    transform: scale(1.2,1.2);\r\n}\r\n\r\n\r\nimg.materialboxed.initialized.active{\r\n  max-width: 100vw;\r\n  /* aqui eu estou desativando o tamanho máximo das figuras para quando elas abrirem não ficarem pequenas e/ou distorcidas */\r\n}\r\n\r\n\r\nimg.materialboxed.initialized.active:hover{\r\n  transform: scale(1,1);\r\n  /* desativando o aumento de scale com hover sempre que elas forem abertas */\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"